.App {
  text-align: center;
  margin-top: 50px;
}

h1 {
  font-size: 2.5rem;
  margin-bottom: 30px;
}

label {
  display: block;
  margin-bottom: 20px;
  font-size: 1.2rem;
}

input[type='number'] {
  font-size: 1.2rem;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  margin-left: 10px;
}

button {
  font-size: 1.2rem;
  padding: 10px 20px;
  background-color: #4caf50;
  color: #fff;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  margin-top: 20px;
}

button:hover {
  background-color: #3e8e41;
}

p {
  font-size: 1.5rem;
  margin-top: 30px;
}
